import React from 'react'

import './StarRating.scss'

export const StarRating = ({ starLimit, starSelected, onRate }) => {
  const stars = [...new Array(starLimit)].map((star, i) => {
    const starClass = i < starSelected - 1 ? "star selected": "star"
    return (
      <div className={starClass}
        key={i}
        onClick={onRate(i + 1)} />
    )
  })

  return (
    <div className="star-rating-container">
      <div className="star-rating">
        { stars }
      </div>
      <p>{starSelected} / {starLimit}</p>
    </div>
  )
}



